import React, { Component } from "react";
import { Card, Button, Row, Container, Col } from "react-bootstrap";
import { Redirect } from "react-router-dom";
import runtimeEnv from "@mars/heroku-js-runtime-env";
const env = runtimeEnv();
export default class Home extends Component {
  state = {
    redirect: false,
    to: "",
  };
  setRedirect = (url) => {
    this.setState({
      redirect: true,
      to: url,
    });
  };
  renderRedirect = () => {
    console.log(this.state.redirect);
    if (this.state.redirect) {
      return <Redirect to={this.state.to} />;
    }
  };
  componentDidMount() {
    const favicon = document.getElementById("FavIcon");
    if (
      env.REACT_APP_FAVICONURL !== undefined ||
      env.REACT_APP_FAVICONURL !== ""
    )
      favicon.href = `${env.REACT_APP_FAVICONURL}`;
    const title = document.getElementById("AppTitle");
    if (env.REACT_APP_TITLE !== undefined || env.REACT_APP_TITLE !== "")
      title.innerText = `${env.REACT_APP_TITLE}`;
    else {
      title.innerText = "App";
    }
  }
  render() {
    return (
      <div>
        {this.renderRedirect()}
        <Card
          style={{
            position: "absolute",
            left: "50%",
            top: "50%",
            transform: "translate(-50%, -50%)",
          }}
          className="text-center"
        >
          <Card.Header>Select login option</Card.Header>
          <Card.Body>
            <Container fluid>
              <Row style={{ padding: 20 }}>
                <Col>
                  <Button
                    size="lg"
                    variant="outline-danger"
                    onClick={() => this.setRedirect("/Instagram")}
                  >
                    INSTAGRAM
                  </Button>
                </Col>
                <Col>
                  <Button
                    size="lg"
                    variant="outline-primary"
                    onClick={() => this.setRedirect("/LinkedIn")}
                  >
                    LinkedIN
                  </Button>
                </Col>
              </Row>
            </Container>
          </Card.Body>
        </Card>
        <Card title="Select login options"></Card>
      </div>
    );
  }
}
